#experience {
    height: 100%;
  }
  
  .experience__container {
    display: flex;
    justify-content: center;
    gap: 5em;
  margin: 0 5em;
  
    & > div {
      background: var(--clr-dark);
    padding: 2rem 0;
      border-radius: 2rem;
      border: 1px solid transparent;
      transition: var(--transition);
  
      &:hover {
        background: transparent;
        border-color: var(--clr-dark);
        cursor: default;
      }
  
      h3 {
        text-align: center;
        margin-bottom: 2rem;
        color: var(--clr-primary);
      }
  
  
    }
  
    .experience__content {
     width: 100%;
  
  
     article {
  margin: 1em 3em;
    }
    
     .experience__details {
      display: flex;
      gap: 1rem;
    
      &-icon {
        margin-top: 6px;
        color: var(--clr-primary);
      }
    }
    }
    }
  
  
  
  
  
  
  
  .experience__languages,
  .experience__tools,
  .experience__libraries {
      width: 100%;
  }
  
  
  /* Media Queries (Small Devices)*/
  
    
    /* Media Queries (Medium Devices) */
    @media screen and (max-width: 1140px) {
  
    .experience__container{
     flex-wrap: wrap;
     margin: 0 3em;
     gap: 3em;
     
  .experience__languages,
  .experience__tools,
  .experience__libraries {
      width: 45%;
  }
  
    }
  
  
    }
    
    @media screen and (max-width: 700px) {
      .experience__container  {
    flex-direction: column;
    align-items: center;
    
    .experience__languages,
    .experience__tools,
    .experience__libraries {
        width: 75%;
    }
      }
      
      }