:root {
  --clr-white: #fff;
  --clr-primary: rgba(0, 187, 255, 0.911);
  --clr-dark: #3b1d8d7a;
  --clr-bg: rgba(28, 19, 66, 0.979); //rgb(20, 20, 61);
  --clr-black: rgb(0, 0, 0);
  --clr-light: rgb(192, 185, 185);
  --clr-neutral-400: #c1bfd3;

  --container-width-lg: 75%;
  --container-width-md: 85%;
  --container-width-sm: 90%;

  --font-size-200: 0.875rem;
  --font-size-300: 0.975rem;
  --font-size-400: 1.3rem;
  --font-size-450: 1.4rem;
  --font-size-500: 2.5rem;

  --padding-header-200: 0.75em;
  --padding-header-300: 1.5em;
  --padding-header-400: 2.5em;
  --padding-header-500: 3em;

  --margin-sections: 8em;

  --border-radius: 35px;

  --transition: all 400ms ease;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  background: var(--clr-bg);
  color: var(--clr-white);
  line-height: 1.7;
}

/* General Styles */
.container {
  width: var(--container-width-lg);
  margin: 0 auto;

}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: var(--font-size-500);
}

h5 {
  font-size: var(--font-size-300);
}

section {
  margin-top: 8rem;
  // height: 100vh; 
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--clr-light);
}

section > h2 {
  color: var(--clr-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--clr-white);
}

a {
  color: var(--clr-primary);
  transition: var(--transition);

  &:hover {
    color: var(--clr-white);
  }
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--clr-primary);
  padding: 0.75rem 1.5rem;
  border-radius: 1.7rem;
  cursor: pointer;
  border: 1px solid var(--clr-primary);
  transition: var(--transition);

  :hover {
    background: var(--clr-white);
    color: var(--clr-dark);
    border-color: transparent;
  }

  &-primary {
    background: var(--clr-primary);
    color: var(--clr-bg);
  }
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* Media Queries (Small Devices) */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }

  section {
    margin-top: 2rem;
  }
}

/* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}
