#portfolio {
    height: 100%;
  }
  
  .portfolio {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  
    &--box {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 2em 4em;
      background-color: var(--clr-dark);
      padding: 1.5em;
      border-radius: var(--border-radius);
  
      &:hover {
        background-color: transparent;
        box-shadow: rgba(41, 99, 133, 0.527) 0px 10px 20px;
      }
  
      h5 {
        font-size: var(--font-size-450);
        padding: 0.7em 0;
      }
  
      img {
        width: 350px;
        height: auto;
        border-radius: var(--border-radius);
      }
  
      a {
        margin: 0.8em;
      }
    }
  }
  