#about {
    height: 100%;
}

.about__container {
    display: flex;
    gap: 10%;
    width: 80%;
    text-align: justify;
}

.about__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    width: 50%;
    gap: 7%;

    p {
font-size: var(--font-size-400);
margin: 2rem 0;
    }
}

.about__me {
    width: 50%;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--clr-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
transition: var(--transition);
    &:hover {
transform: rotate(0);
    }
}

/* Media Queries (Small Devices) */

@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }
  }
  
  /* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
   .about__me {
       width: 50%;
       margin: 2rem auto 4rem;
   }

   .about__container {
       flex-direction: column;
   }

   .about__content {
       width: 100%;
   }
  }

  