#header {
    height: 100%;
  }
  
  header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
  }
  
  .header__container {
    text-align: center;
    height: 100%;
    position: relative;
  }
  
  /* CTA */
  
  .cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.25rem;
    justify-content: center;
  }
  
  /* Header Socials */
  
  .header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
  
    &::after {
      content: "";
      width: 1px;
      height: 2rem;
      background-color: var(--clr-primary);
    }
  }
  
  /* Me */
  
  .me {
    background: linear-gradient(var(--clr-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 0.7rem 1.5rem 1rem 1.5rem;
  }
  
  /* Scroll Down */
  
  .scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
  }
  
  /* Media Queries (Small Devices)*/
  @media screen and (max-width: 600px) {
    .header {
      height: 100vh;
  
      &__socials {
        display: none;
      }
    }
  
    .scroll__down {
      display: none;
    }
  }
  